import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Summary } from './models/summary';
import { Observable } from 'rxjs';

@Injectable()
export class AccountsService {

  constructor(private http: HttpClient) { }

  //private  _url:string='/assets/data/summary.json';
  private base_url='http://apar-apar-v1.apps.us-west-1.starter.openshift-online.com/'
  private  _url:string= this.base_url+'accounts/api/v1/summery';
  private  pay_url:string=this.base_url+'accounts/api/v1/payables?date=';
  private  receive_url:string=this.base_url+'/accounts/api/v1/receivables?date=';

  getSummaryData():Observable<Summary[]> {
    return this.http.get<Summary[]>(this._url);
  }
  getPayabales(selecteddate:string):Observable<[]> {
    return this.http.get<[]>(this.pay_url+selecteddate);
  }
  getReceivables(selecteddate:string):Observable<[]> {
    return this.http.get<[]>(this.receive_url+selecteddate);
  }
}
