import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { AccountsService } from '../accounts.service';

@Component({
  selector: 'app-account-summary',
  templateUrl: './account-summary.component.html',
  styleUrls: ['./account-summary.component.css'],
  providers:[AccountsService]
})
export class AccountSummaryComponent implements OnInit {

  public summary = [];

  public totalPays=[];
 
  @Output() public total_receivables = new EventEmitter<[]>();

  constructor(private accountSummary : AccountsService){}
  
  @Output() payDate = new EventEmitter<string>();
  
  ngOnInit() {
    this.accountSummary.getSummaryData().subscribe(data => this.summary = data)
  }

  getDetailedPayables(event,item){
    alert("index"+item.date);
    //this.accountSummary.getPayabales(item.date).subscribe(data=>this.totalPays=data)
    this.payDate.emit(item.date)
    //t.select('pay-tab')
  }


  getDetailedReceivables(event,item){
    alert("index"+item.date);
    this.payDate.emit(item.date)
  }
}
