import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { AccountsService } from '../accounts.service';

@Component({
  selector: 'app-account-receivables',
  templateUrl: './account-receivables.component.html',
  styleUrls: ['./account-receivables.component.css'],
  providers:[AccountsService]
})
export class AccountReceivablesComponent implements OnInit {

  public receivables:[]
  constructor(private accountService:AccountsService) { }

  @Input() event: string;
  ngOnInit() {
  
    //this.accountService.getReceivables().subscribe(data=>this.receivables = data)
  }

  ngOnChanges(changes: SimpleChanges) {
    this.accountService.getReceivables(this.event).subscribe(data=>this.receivables=data)
  }
}
